.Modal {
    position: fixed;
    z-index: 500;
    background-color: white;
    width: 80%;
    border: 1px solid #ccc;
    box-shadow: 1px 1px 1px black;
    padding: 16px;
    left: 10%;
    top: 20%;
    box-sizing: border-box;
    transition: all 0.3s ease-out;
    height:40%;
    overflow-y: hidden;

}

@media (min-width: 600px) {
    .Modal {
        width: 400px;
         left: calc(50% - 200px);
        top: 30%;
        height:30%;
    }
}